import { reactive, ref } from 'vue';
export default {
  name: 'AddProject',
  props: ['data'],

  setup(props, {
    emit
  }) {
    const formRef = ref();
    const form = ref({ ...props.data
    });

    const changeText = val => {
      form.value.text = val;
    };

    const rules = reactive({
      project: [{
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }],
      inProject: [{
        required: true,
        message: '请选择您的项目开始时间'
      }],
      outProject: [{
        required: true,
        message: '请选择您的项目结束时间'
      }]
    });

    const removeProject = () => {
      emit('removeProject');
    };

    const isRulesTrue = () => {
      if (!formRef.value) return;
      return formRef.value.validate(valid => {
        return valid ? true : false;
      });
    };

    return {
      formRef,
      form,
      rules,
      changeText,
      removeProject,
      isRulesTrue
    };
  }

};