import { reactive, ref } from 'vue';
export default {
  name: 'AddEducation',
  props: ['data'],

  setup(props, {
    emit
  }) {
    const formRef = ref();
    const form = ref({ ...props.data
    });
    const rules = reactive({
      college: [{
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }],
      inDate: [{
        required: true,
        message: '请选择您的入学时间'
      }],
      outDate: [{
        required: true,
        message: '请选择您的毕业时间'
      }]
    });

    const removeEducation = () => {
      emit('removeEducation');
    };

    const isRulesTrue = () => {
      if (!formRef.value) return;
      return formRef.value.validate(valid => {
        return valid ? true : false;
      });
    };

    return {
      formRef,
      form,
      rules,
      removeEducation,
      isRulesTrue
    };
  }

};