import 'element-plus/es/components/message-box/style/css';
import { ElMessageBox } from 'element-plus';
import ExportResumeMain from '../main/ExportResumeMain.vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import { ref, reactive, toRefs, onMounted } from 'vue';
import isPC from '@/hooks/isPC';
export default {
  name: 'HeaderMain',
  components: {
    ExportResumeMain
  },

  setup() {
    const route = useRoute();
    const store = useStore();
    const equipmentIsPC = ref(isPC());
    let dragData = ref();
    const hiddenAside = ref(false);
    const form = reactive({
      ptpb: 30,
      prpl: 50,
      showAge: true,
      nameSize: 32,
      fontSize: 16,
      lineHeight: 16,
      h2Size: 24,
      moduleMt: 22,
      nodeMt: 24,
      h2Mb: 23,
      h3Size: 20,
      h4Size: 18
    });

    const formError = () => {
      ElMessageBox.alert('请检查是否有必填项为空，并且格式正确', '提交失败', {
        confirmButtonText: '确定'
      });
    };

    const dragEvent = reactive({
      oldData: null,
      newData: null,
      changeIndex: [],
      dragstart: value => {
        dragEvent.oldData = value;
      },
      dragenter: (value, e) => {
        dragEvent.newData = value;
        e.preventDefault();
      },
      dragend: () => {
        if (dragEvent.oldData !== dragEvent.newData) {
          let oldIndex = dragData.value.indexOf(dragEvent.oldData);
          let newIndex = dragData.value.indexOf(dragEvent.newData);
          dragEvent.changeIndex = [oldIndex + 1, newIndex + 1];
          let newItems = [...dragData.value];
          newItems.splice(oldIndex, 1);
          newItems.splice(newIndex, 0, dragEvent.oldData);
          dragData.value = [...newItems];
        }
      },
      dragover: e => {
        e.preventDefault();
      }
    });
    const refDrageEvent = toRefs(dragEvent);
    onMounted(() => {
      if (route.query.sort) {
        dragData.value = store.getters.getResumeData(route.query.sort);
      } else {
        dragData.value = store.getters.getResumeData();
      }
    });
    return {
      route,
      equipmentIsPC,
      form,
      dragData,
      hiddenAside,
      formError,
      ...refDrageEvent
    };
  }

};