import { reactive, ref } from 'vue';
export default {
  name: 'AddCustomModel',
  props: ['data'],

  setup(props, {
    emit
  }) {
    const formRef = ref();
    const form = ref({ ...props.data
    });

    const changeText = val => {
      form.value.text = val;
    };

    const rules = reactive({
      title: [{
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }]
    });

    const removeModel = () => {
      emit('removeModel');
    };

    const isRulesTrue = () => {
      if (!formRef.value) return;
      return formRef.value.validate(valid => {
        return valid ? true : false;
      });
    };

    return {
      formRef,
      form,
      rules,
      changeText,
      removeModel,
      isRulesTrue
    };
  }

};