import { computed, onMounted, ref } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { ElMessageBox } from 'element-plus';
import fadeIn from '@/hooks/fadeIn';
import { decrypt } from '@/hooks/crypto';
export default {
  name: 'HomeMain',

  setup() {
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const hasResume = computed(() => {
      return store.state.step1Data.name ? true : false;
    });
    const btnGroup = ref();

    const removeClass = () => {
      for (let index = 0; index < btnGroup.value.children.length; index++) {
        const element = btnGroup.value.children[index];
        element.classList.remove("active");
        element.classList.remove("inactive");
      }
    };

    onMounted(() => {
      if (route.query.id) {
        const sortNum = route.query.sort ? `?sort=${route.query.sort}` : '';
        fullscreenLoading.value = true;
        fetch(`${process.env.BASE_URL}/${route.query.id}.json`).then(response => {
          response.text().then(jsonContent => {
            const decryptData = decrypt(jsonContent);
            store.commit('commitAllData', JSON.parse(decryptData));

            if (document.body.offsetWidth >= 1140) {
              router.push(`/exportResume${sortNum}`);
            } else {
              router.push(`/showResume${sortNum}`);
            }
          });
        }).finally(() => {
          fullscreenLoading.value = false;
        });
      }

      if (!localStorage.getItem('hiddenAppTips') && !sessionStorage.getItem('tHiddenAppTips')) {
        ElMessageBox.confirm('本网站为为个人性质的简历编辑导出网站，没有任何网络接口请求，页面加载完成后断网也可继续操作，绝对安全私密，尽可放心使用。<br><strong>桌面端：</strong>可使用该网站全部功能。<br><strong>移动端：</strong>只能使用编辑页面的功能，不太兼容导出pdf的功能，(移动端可使用编辑页面导出您的简历数据之后,再在桌面端导入数据再导出pdf。)<br>如有建议或反馈请联系我：<a style="color: #409EFF;text-decoration: underline;" href="mailto:32413082@qq.com">32413082@qq.com</a>', '小提示', {
          confirmButtonText: '确定',
          cancelButtonText: '不再提示',
          draggable: true,
          dangerouslyUseHTMLString: true,
          distinguishCancelAndClose: true
        }).catch(status => {
          if (status === 'cancel') {
            localStorage.setItem('hiddenAppTips', 'true');
          }
        }).finally(() => {
          sessionStorage.setItem('tHiddenAppTips', 'true');
        });
      }

      for (let i = 0; i < btnGroup.value.children.length; i++) {
        const element = btnGroup.value.children[i];
        element.addEventListener("mouseenter", () => {
          element.classList.add("active");

          for (let j = 0; j < btnGroup.value.children.length; j++) {
            const element1 = btnGroup.value.children[j];

            if (i !== j) {
              element1.classList.add("inactive");
            }
          }
        });
      }

      fadeIn();
    });

    const newResume = () => {
      router.push('/edit/step1');
    };

    const fullscreenLoading = ref(false);

    const exResume = () => {
      fullscreenLoading.value = true;
      setTimeout(() => {
        fullscreenLoading.value = false;

        if (hasResume.value) {
          router.push('/exportResume');
        } else {
          ElMessageBox.confirm('暂无本地简历缓存，请跳转到新建简历页面新建简历。', '提示', {
            confirmButtonText: '跳转',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            router.push('/edit/step1');
          }).catch(() => {});
        }
      }, 1000);
    };

    return {
      btnGroup,
      hasResume,
      removeClass,
      newResume,
      fullscreenLoading,
      exResume
    };
  }

};