import { onMounted, reactive, ref } from 'vue';
import Step from '../ui/Step.vue';
import 'element-plus/es/components/message/style/css';
import 'element-plus/es/components/cascader/style/css';
import { ElMessage } from 'element-plus';
import { useRouter } from 'vue-router';
import { useStore } from 'vuex';
import { EluiChinaAreaDht } from 'elui-china-area-dht';
import { nanoid } from 'nanoid';
import fadeIn from '@/hooks/fadeIn';
export default {
  name: 'BasicPage',
  components: {
    Step,
    EluiChinaAreaDht
  },

  setup(props, {
    emit
  }) {
    const router = useRouter();
    const store = useStore();
    const step1Data = store.state.step1Data;
    const formRef = ref();
    const form = reactive({
      id: '',
      name: '',
      sex: '',
      birthday: '',
      phoneNumber: '',
      email: '',
      JobObjective: '',
      currentState: '',
      wechat: '',
      github: '',
      address: [],
      avatar: ''
    });
    onMounted(() => {
      Object.keys(form).forEach(i => {
        form[i] = step1Data[i] || form[i];
      });
      if (!form.id) form.id = nanoid();
      fadeIn();
    });
    const rules = reactive({
      name: [{
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }],
      sex: [{
        required: true,
        message: '请选择您的性别'
      }],
      phoneNumber: [{
        pattern: /^1[3-9]\d{9}$/,
        message: '手机号为11位阿拉伯数字'
      }],
      email: [{
        type: 'email',
        message: '请输入正确的邮箱地址',
        trigger: ['blur', 'change']
      }],
      wechat: [{
        pattern: /^[a-zA-Z][a-zA-Z\d_-]{5,19}$/,
        message: '微信号格式不正确'
      }]
    });

    const handleAvatarSuccess = response => {
      if ((response.raw.type === 'image/jpeg' || response.raw.type === 'image/png') && response.raw.size / 1024 / 1024 <= 1) {
        // 文件转base64
        const reader = new FileReader();
        reader.readAsDataURL(response.raw);

        reader.onload = e => {
          form.avatar = e.target.result;
        }; // form.avatar = URL.createObjectURL(response.raw)

      }
    };

    const beforeAvatarUpload = rawFile => {
      if (rawFile.type !== 'image/jpeg' && rawFile.type !== 'image/png') {
        ElMessage.error('照片必须是jpg/png格式的');
        return false;
      } else if (rawFile.size / 1024 / 1024 > 2) {
        ElMessage.error('照片不能超过2Mb，请更换图片或压缩图片');
        return false;
      }

      return true;
    };

    const removeAvata = () => {
      form.avatar = '';
    };

    const onSubmit = formEl => {
      if (!formEl) return;
      formEl.validate(valid => {
        if (valid) {
          store.commit('commitStep1Data', form);
          router.push('/edit/step2');
        } else {
          emit('formError');
          return false;
        }
      });
    };

    const goBack = () => {
      router.push('/');
    };

    return {
      formRef,
      form,
      rules,
      handleAvatarSuccess,
      beforeAvatarUpload,
      removeAvata,
      onSubmit,
      goBack
    };
  }

};