import { reactive, ref } from 'vue';
export default {
  name: 'AddWork',
  props: ['data'],

  setup(props, {
    emit
  }) {
    const formRef = ref();
    const form = ref({ ...props.data
    });

    const changeText = val => {
      form.value.text = val;
    };

    const rules = reactive({
      company: [{
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }],
      inJob: [{
        required: true,
        message: '请选择您的入职时间'
      }]
    });

    const removeWork = () => {
      emit('removeWork');
    };

    const isRulesTrue = () => {
      if (!formRef.value) return;
      return formRef.value.validate(valid => {
        return valid ? true : false;
      });
    };

    return {
      formRef,
      form,
      rules,
      changeText,
      removeWork,
      isRulesTrue
    };
  }

};